.row_poster{
    width:100%;
    object-fit: contain;
    max-height: 100px;
    transform: transform 450ms;
    margin-right: 15px;
}
.row_posters{
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
}
.row_posters::-webkit-scrollbar{
    display: none;
}
.topic{
    color: white;
    margin-left: 20px;
}
.row_poster:hover{
    transform: scale(1.08);
}
.row_poster_Larger{
    max-height: 250px;
} 
.row_poster_Larger:hover{
    transform: scale(1.09);
}
.row{
    margin-left: 20px;
    color: white;
}